.image {
  width: 1170px;
  margin: 0 auto;
  img {
    width: 1170px;
    height: 100%;
    object-fit: cover;
  }
}

@media screen and (max-width: 1200px) {
  .image {
    width: 770px;
    margin: 0 auto;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

@media screen and (max-width: 768px) {
  .image{
    // margin-top: 83px;
    width: 320px;
    height: 150px;
  }
}
